body {
  background-color: azure;
  background-image: url("https://img.freepik.com/free-vector/yellow-blue-wave-gradient-background-vector_53876-128354.jpg?w=1060&t=st=1685911978~exp=1685912578~hmac=9933ba395962ba3afe8b451c0abdcff9a23383fdbe0fd2d236c240c944a6cb5d");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
  margin-top: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.form-position {
  text-align: center;
  width: 100%;
  background-color: #075494;
  border-radius: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  box-shadow: 0 4px 12px rgba(0, 0, 0, .2), 0 16px 20px rgba(0, 0, 0, .2);
}

.unvisible, .loader {
  display: none;
}

.gallary {
  width: 100%;
  grid-template-columns: repeat(4, 380px);
  gap: 10px;
  margin: 0 auto;
  padding: 0 10px;
  display: grid;
}

.btn-submit {
  color: #050551;
  background-color: #57edfe;
  border: 0;
  border-radius: 5px;
  font-weight: 700;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

input {
  border-color: #00f;
  border-radius: 5px;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

